.flyout {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  justify-content: space-between;
}

section {
  padding: 4rem 0 4rem 0;
}

.d-grid {
  display: grid !important;
}

// Custom colors
.lupi-blue {
  background-color: $lupiBlue !important;
  &.darken-1 {
    background-color: darken($lupiBlue, 5%) !important;
  }
  &.darken-2 {
    background-color: darken($lupiBlue, 10%) !important;
  }
  &.darken-3 {
    background-color: darken($lupiBlue, 15%) !important;
  }
  &.lighten-1 {
    background-color: lighten($lupiBlue, 10%) !important;
  }
  &.lighten-2 {
    background-color: lighten($lupiBlue, 20%) !important;
  }
  &.lighten-3 {
    background-color: lighten($lupiBlue, 30%) !important;
  }
}
.btn-lupi-blue {
  text-transform: initial;
  background-color: $lupiBlue;
  color: white;
  &:hover {
    background-color: darken($lupiBlue, 5%);
    color: white;
  }
  &:focus {
    background-color: darken($lupiBlue, 10%);
    color: white;
  }
}
.blue-text {
  color: $lupiBlue !important;
}

// Custom buttons
.btn {
  box-shadow: none;
  &:hover {
    box-shadow: none;
  }
  &:focus {
    box-shadow: none;
  }
}
.btn-rounded {
  border-radius: 10px !important;
}

// Scrollbar
::-webkit-scrollbar {
  width: 12px;
  background-color: #f5f5f5;
}
::-webkit-scrollbar-thumb {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #555;
}
::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #f5f5f5;
}

.clickable {
  cursor: pointer;
}

.flex-wrap-wrap {
  flex-wrap: wrap;
}

